@import './variables.scss';

* {
  box-sizing: border-box;
}

.btn{
  border:0;
  outline:0;
  cursor:pointer;

  &_primary{
    width:150px;
    padding:5px;
    background: #002051;
    color:white;
    border:1px solid transparent;

    &:hover{
      background:white;
      color: #002051;
      border: 1px solid $primary;
    }
  }

  &_small{
    background: $secondary;
    width:30px;
    height:30px;

    &:hover{
      opacity:0.7;
    }
  }
}