.cart-item{
  padding:40px;
  margin-bottom:20px;
  position: relative;
  border-bottom: 1px solid #ccc;

  &__body{
    display:flex;
    justify-content: space-between;
    height:100px;
  }

  &__price-for-one{
    font-size:12px;
    font-style: italic;
  }

  &__actions{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
  }

  .cart-item__delete-btn{
    background: url('../../assets/icons/delete_btn.svg') 50% 50% no-repeat;

    &:hover{
      opacity: 0.6;
    }
  }
}
