.quantity-switcher{
  display:flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
  border: 1px solid #002051;
  border-radius: 0 2px 2px 0;

  &__current{
    width:30px;
    height:30px;
    text-align:center;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__btn{
    width:30px;
    height:30px;
    display:flex;
    justify-content: center;
    align-items: center;
    cursor:pointer;
  }

}